<template>
  <div class="wrapper">
    <template>
      Scroll down to see the bottom-right button.
      <el-backtop target='.wrapper'></el-backtop>
    </template>
    <exampleCom title="">
      <template v-slot:1>
        <commonTable :tableMap="example1.tableMap" :tableData="example1.tableData" :tableConfig="example1.tableConfig"
          @viewDetails="viewDetails" @selection-change="selectChange" />
      </template>
      <template v-slot:2>
        <h4>slot</h4>
        <p>, </p>
        <commonTable :tableMap="example2.tableMap" :tableData="example2.tableData" @editEject="editEject">
          <template v-slot:btn="{ rowData, template }">
            <el-button v-for="btn in template.btn" :key="btn.label" :type="btn.type || 'primary'"
              :style="rowData.isEdit ? btn.style: {'background':'red'}" :size="btn.size || 'mini'"
              @click="vmSelf[btn.event](rowData)" v-text="getBtnLabel(rowData, btn)" />
          </template>
        </commonTable>
      </template>
      <template v-slot:3>
        <h4>slot</h4>
        <commonTable :tableMap="example3.tableMap" :tableData="example3.tableData" @editEject="editEject">
          <template v-slot:btn="{ rowData, template, scope }">
            <template v-for="(item,key) in template">
              <el-input :key="key" v-if="key == 'input'" v-model="example3.input[scope.$index]" placeholder="">
              </el-input>
              <el-select :key="key" v-if="key == 'select'" v-model="example3.value[scope.$index]" placeholder="">
                <el-option v-for="item in example3.options" :key="item.value" :label="item.label" :value="item.value" />
              </el-select>
            </template>
          </template>
        </commonTable>
      </template>
    </exampleCom>

    <testMD />
  </div>
</template>
<script>
  import testMD from "@/assets/markdown/components/table.md";
  import commonTable from "@/components/Common/table.vue";
  import exampleCom from "@/components/documentComponents/example.vue";

  // 1
  const mixin1 = {
    data: () => {
      return {
        example1
      }
    },
    methods: {
      viewDetails() {
        console.log("is details");
      },
      selectChange(val) {
        console.log(val);
      }
    }
  }
  // 1
  const example1 = {
    tableData: {
      data: [{
          prop1: "No description",
          prop2: "website",
          prop3: "topics",
        },
        {
          prop1: "published",
          prop2: "passive",
          prop3: "Releases",
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        name: "",
        prop: "prop1",
        align: "right"
      },
      {
        name: "",
        prop: "prop2",
        align: "left"
      },
      {
        name: "",
        prop: "prop3"
      },
      {
        name: "",
        width: 300,
        template: {
          btn: [{
            label: "",
            style: "background: green",
            event: "viewDetails"
          }]
        }
      }
    ],
    tableConfig: {
      // , 
      hidePage: false, // , false
      hideIndex: false, // , false
      showCheckBox: true // , false
    }
  }


  // 2
  const mixin2 = {
    data: () => {
      return {
        example2
      }
    },
    methods: {
      editEject(data) {
        console.log('is editEject', data.isEdit ? '' : '')
      },
      getBtnLabel(rowData, btn) {
        return (rowData.isEdit ? '' : '');
      }
    },
    computed: {
      vmSelf() {
        return this;
      }
    }
  }
  // 2
  const example2 = {
    tableData: {
      data: [{
          isEdit: true,
        },
        {
          isEdit: false,
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        type: "selection",
      }, {
        name: "",
        prop: "isEdit",
      },
      {
        name: "",
        width: 300,
        template: {
          btn: [{
            label: "",
            style: "background: blue; border: none;",
            event: "editEject"
          }]
        }
      }
    ]
  }



  // 3
  const mixin3 = {
    data: () => {
      return {
        example3,
      }
    }
  }
  // 3 data
  const example3 = {
    tableData: {
      data: [{
          comp: 'select'
        },
        {
          comp: 'input'
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        name: "",
        prop: "comp",
        // template, prop
        template: {
          input: {
            event: 'change'
          }
        }
      },
      {
        name: "",
        template: {
          select: {
            event: 'change'
          }
        }
      }
    ],
    input: {},
    value: {},
    options: [{
      value: '1',
      label: ''
    }, {
      value: '2',
      label: ''
    }, {
      value: '3',
      label: ''
    }, {
      value: '4',
      label: ''
    }, {
      value: '5',
      label: ''
    }],
  }

  export default {
    components: {
      testMD,
      commonTable,
      exampleCom
    },
    mixins: [mixin1, mixin2, mixin3],
  };
</script>
<style lang="scss" scoped>
  .wrapper {
    width: calc(100% - 1px);
  }
</style>